
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

export default {
components: {
Group,
ViewBox,
XInput,
XButton,
XHeader,
Box,
Flexbox,
FlexboxItem
},
data () {
return {
telNum: '',
vcode: '',
reget: '',
password: '',
confirmPassword: '',
waiting: false,
counter: 59
}
},
methods: {
goLogin () {
this.$router.push({name: 'login'})
},
doResetPasswd () {
console.log('do reset')
if (this.$refs.input1.valid && this.$refs.input2.valid && this.$refs.input3.valid && this.$refs.input4.valid) {
if (this.password === this.confirmPassword) {
this.$api.userApi.resetLoginPasswordBySms(this.telNum.replace(/[ ]/g, ''), this.password, this.vcode).then(result => {
if (result.data.success) {
this.$vux.toast.show({
type: 'success',
text: ''
})
this.$router.replace({name: 'login'})
} else {
this.$vux.toast.show({
type: 'text',
text: result.data.message
})
}
})
} else {
this.$vux.toast.show({
type: 'text',
text: '',
width: '11em'
})
}
} else {
this.$vux.toast.show({
type: 'text',
text: '',
width: '10em'
})
}
},
nextStep () {
this.$router.push({name: 'home'})
},
getSmsCode () {
if (this.$refs.input1.valid) {
console.log(this.telNum)
console.log(this.telNum.replace(/[ ]/g, ''))
this.$api.smsApi.getSmsCode(this.telNum.replace(/[ ]/g, ''), 'PHONECHANGEPWD').then(result => {
if (result.data.success) {
this.waiting = true
this.timer = setInterval(this.changeBtnText, 1000)
this.$vux.toast.show({
type: 'success',
text: ''
})
} else {
this.$vux.toast.show({
type: 'warn',
text: result.data.message
})
}
})
} else {
this.$vux.toast.show({
type: 'text',
text: '',
width: '10em'
})
}
console.log('sdf')
},
changeBtnText () {
this.reget = '(' + this.counter + ')'
this.counter --
if (this.counter < 0) {
this.waiting = false
this.reget = ''
window.clearInterval(this.timer)
this.counter = 59
}
}
}
}



